"use client";
import React, { useState } from "react";
import { ChevronDown } from "lucide-react";

function DropDown({
  items,
  onClick,
}: {
  items: string[];
  onClick: (index: number) => void;
}) {
  const [selectedItem, setSelectedItem] = useState(items[0]);
  const [open, setOpen] = useState(false);

  return (
    <div className="relative w-full">
      <button
        className={`rounded-xl w-full bg-white border-1 border-black/25 py-5 px-8 flex items-center justify-between `}
        onClick={() => setOpen((prev) => !prev)}
      >

        <span className="text-xl ">{selectedItem}</span>
        <ChevronDown  size={24} />
      </button>
      {open && (
        <ul className="absolute z-10 w-full shadow space-y-0 ">
          {items.filter((el) => selectedItem !== el).map((item) => (
            <li
              onClick={() => {
                setSelectedItem(item);
                onClick(items.indexOf(item));
                setOpen(false);
              }}
              className={`relative w-full bg-white py-2 px-4 flex items-center justify-between hover:bg-gray-50 cursor-pointer after:absolute after:bottom-0 after:left-4 after:right-4 after:h-px after:bg-black/10 last:after:hidden
            `}
              key={item}
            >
              <h2 className="text-lg"> {item}</h2>
              <ChevronDown color="#fff" size={16} />
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}

export default DropDown;
